* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

.container {
  max-height: 100dvh;
  max-width: 100dvw;
  position: relative;
}

.carousel {
  height: 100%;
  background: #5f5f5f;
  flex-flow: row;
  align-items: center;
  display: flex;
}

.carousel .slide {
  width: 100%;
  max-height: 100dvh;
  background: #fafafa;
  display: none;
  overflow: hidden;
}

.carousel .slide.active {
  transition: opacity .5ms ease-in;
  display: flex;
}

.carousel .slide img {
  max-width: auto;
  max-height: 100vh;
  max-height: 100dvh;
  object-fit: cover;
  align-self: center;
  margin: 0 auto;
}

