}

.main-btn.previous {
  left: var(--x-padding);
  width: 0;
  height: 0;
  border-top: var(--border-size) solid transparent;
  border-bottom: var(--border-size) solid transparent;
  border-right: var(--border-size-pointer) solid var(--border-color);
}

